@import "../../../scss/index";
// modal overlay and content styles
.overlay {
 @include overlay;
}

.modalContent {
    @include modal;
    div {font-size:2rem;border-bottom: 1px solid #ccc; padding:1rem;
    margin-bottom:1rem;}
    .closeButton {
        color:$delete-link;
        position: absolute;
        top:1rem;
        right:1rem;
    }
    .reloadButton {
        color:$edit-link;

        position: absolute;
        top:1rem;
        right:5rem;
    }
}