@mixin flex($dir: row, $jc: center, $ai: center) {
    display: flex;
    flex-direction: $dir;
    justify-content: $jc;
    align-items: $ai;
    @content;
  }

  @mixin buttons-bg($color){
    background-color: $color;
    &:hover {
      background-color: lighten($color: $color, $amount: 10%);
    }

  
  }

    
  @mixin modal {width:60vw;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
    left:50%;
    top:50%;
    position:fixed;
    transform: translate(-50%,-50%);
    background-color: $modal-bg;
    padding: 4rem;}

    @mixin promptMessage {
      .reloadMessage{
        text-align: center;
        font-size:3rem;
        padding:4rem;
        div {
            margin-bottom:4rem;
        }
        button {
            width:100%;
        
        }
    }
    }

    @mixin overlay {
      background-color: rgba($color: black, $alpha: 0.6);
      width: 100%;
      height: 100%;
      position: fixed;
      top: 0;
      left: 0;
    }