@import "../../../../scss/index";

.formContainer {
    h2 {
      padding: 0;
      margin: 0;
      margin-bottom: 2rem;
      opacity: 0.7;
      text-align: left;
      font-size: 2.5rem;
      width: 100%;
    }
    @include flex($dir: column);
    .closeButton {
      position: fixed;
      right: 1rem;
      color:$delete-link;
          border: none;
      top: 1rem;
     
    }
   .reloadButton {
     position: fixed;
      color:$edit-link;
          border: none;
      top: 1rem;
     right:5rem;
      }
    form {
      width: 100%;
      input[type="submit"] {
        width: 100%;
        padding: 1rem;
        font-size: 2rem;
        color: white;
        @include buttons-bg($add-link);
        border: none;
      }
    }
  
 
  }
  