@import "../../scss/index";
.mainTable {
  width: 80vw;
  box-shadow: rgba(0, 0, 0, 0.25) 0px 25px 50px -12px;
  background-color: white;
  border-radius: 10px;
  overflow: hidden;
  margin: 4rem auto;

  table {
    border-spacing: 0;
  }
  th,
  td {
    width: (80vw)/6;
    text-align: center;
    padding: 1rem 2rem;
    &:nth-child(2) {
      text-align: left;
    }
  }
  tr {
    cursor: pointer;
    @include flex($jc: space-around);
    border-bottom: 1px solid #ccc;
    &:nth-child(even),
    &:last-child {
      background-color: $rows-strip;
    }
    &:hover {
      background-color: $rows-hover;
    }
  }
  thead {
    font-size: 2rem;
    th {
      letter-spacing: 2px;
    }
  }
  tbody {
    td {
      font-size: 1.7rem;
      &:last-child {
        @include flex($jc: flex-start) {
          gap: 2rem;
        }
        span {
          a {
            color: white;
          }
          border-radius: 5px;
          color: white;
          padding: 1rem;
          font-size: 1.3rem;
          @include buttons-bg($delete-link);

          &:first-of-type {
            @include buttons-bg($edit-link);
          }
        }
      }
    }
  }

  .reloadMessage {
    text-align: center;
    font-size: 3rem;
    padding: 4rem;
    div {
      margin-bottom: 3rem;
    }
    button {
      color: black;
      width: 100%;
      outline: none;
      padding: 2rem;
    }
  }
}
