@import "./index";

html {
    font-size:62.5%;
}
* {
    box-sizing: border-box;
}
body {
    background-color: $mainBg;
    
    margin:0;padding:0;
    font-family: 'Roboto', sans-serif;
        * {
        font-family:inherit
    } 
}

a {
    border-radius: 10px;

    text-decoration:none;

}

button {
    padding:1rem;
    font:{
        family:inherit;
        size:1.5rem;
    }
    border:none;
    outline: none;
    border-radius:10px;
    color:white;
}

button,a,input[type="submit"],svg {
    cursor:pointer}

*:focus {
    outline:none
}

.Toastify {
    font-size:1.5rem;
}