@import "../../../scss/index";
.main {
    @include flex($jc:space-between);
    padding:3rem;
    font-size:1.5rem;
    span {
        margin-right:2rem
    }
    .pagination {
        margin:0;padding:0;list-style-type: none;
        a:focus {
            outline:none;
        }
        &Page,&PrevAndNext,&Ellipsis{
            cursor:pointer;
            display:inline;
            border:1px solid #ccc;
            padding:1rem;
        }
        &Active{           

            background-color:$page-active
        }
    }
}