@import "../../scss/index";
.main {
    padding:2rem ;
    background-color: $headerBg;
@include flex($jc:space-between);
h3 {color:white;
    font-size:2.5rem;
    padding:0;margin:0;
    letter-spacing: 3px;
    text-shadow: darkblue;
}
a{
    box-shadow: rgba(0, 0, 0, 0.16) 0px 1px 4px;
    color:white;
padding:2rem 4rem;
font-weight:bold;
font-size:1.8rem;
@include buttons-bg($add-link);
&:visited{color:white}}
div {
    @include flex;

}
}